<template>
	<div id="orders">
		<h1>我的订单</h1>
		<ul class="order-list">
            <li v-for="order in orders" class="order-item">
            	<div class="order-header">
            		订单号:{{order.oid}}
            		时间:{{order.data}}
            	</div>
            	<div class="order-content">
            		{{order.moviename}}
            		{{order.movietime}}
            		{{order.cinema}}
            		{{order.ticketamount}}
            		{{order.price}}
            		{{order.screeningroom}}
            		{{order.seat.oid}}
            	</div>
            </li>
          </ul>
	</div>
</template>

<script>
export default {
	name: 'orders',
	created: function() {
		this.getOrders();
	},
	data() {
		return {
			orders:[{
				oid: '',
				date: '',
				moviename: '',
				movietime: '',
				cinema: '',
				ticketamount: 0,
				price: 0.0,
				screeningroom: 0,
				seat: {
					oid: 0,
					row: 0,
					column: 0,
				}
			}],
			user: 'kaixin'
		}
	},
	methods: {
		getOrders: function() {
			this.$http.get('/order/getorderforms',{params: this.user})
				.then(res => {
					this.orders = res.data;
				})
				.catch(e => {
					console.log(e);
				});
		}
	}
}
</script>

<style>
	h1 {
		margin-left: 100px;
	}
</style>


