<template>
	<div>
		<h1>请选择座位</h1>
		<div id="screen">大屏幕</div>
		<div class="seat-container">
			<p v-for="row in this.seats">
				<span v-for="seat in row" :class="seat.style" @click="selectseat(seat)">
				</span>
			</p>
		</div>
		<p v-if="this.orderSeats.length" class="warning">您已选择<span v-for="item in showSeats">{{item}} </span></p>
		<p v-else class="warning">尚未选择座位</p>
		
		<button class="btn" @click="submitOrder">提交订单</button>
	</div>
</template>

<script>
export default {
	name: 'seats',
	created: function() {
		this.getSeats();
	},
	data() {
		return {
			seats: [[],[],[],[],[],[],[],[],[],[]],
			orderSeats: [],
			showSeats: [],
		}
	},
	methods: {
		getSeats: function() {
			const playid = this.$route.params.playid;
			this.$http.get(`/movie/seat/${playid}`)
				.then((res) => {
					this.seats = res.data;
					this.seats.forEach((item) => {
						if (item.isAvailable) {
							item.style = 'avalible';
						} else {
							item.style = 'sold';
						}
						const flag = item.isAvailable ? true : false;
						this.seats[item.row-1][item.col-1] = {
							selected: false,
							row: item.row,
							col: item.col,
							style: item.style,
							isAvailable: flag,
							seatid: item.seatid,
						}
					})
					console.log(this.seats);
				})
				.catch((e) => {
					console.log(e);
				})
		},
		selectseat: function(seat) {
			console.log(seat);
			const seatsArr = this.seats.slice();
			if (seat.isAvailable && !seat.selected) {
				const newSeat = seat;
				newSeat.selected = true;
				newSeat.style = 'seleceted';
				let str = '第'+seat.row+'排'+seat.col+'座';
				seatsArr[seat.row-1][seat.col-1] = newSeat;
				this.orderSeats.push(seat.id);
				this.showSeats.push(str);
				this.seats = seatsArr;
			} else if (seat.isAvailable && seat.selected) {
				const newSeat = seat;
				newSeat.selected = false;
				newSeat.style = 'avalible';
				seatsArr[seat.row-1][seat.col-1] = newSeat;
				const index = this.orderSeats.findIndex((item) => item === seat.id);
				this.orderSeats.splice(index, 1);
				this.showSeats.splice(index, 1);
				this.seats = seatsArr;
			}
		},
		submitOrder: function() {
			const playid = this.$route.params.playid;
			const data = {
				playid,
				seats: this.orderSeats,
				tickets: this.orderSeats.length,
			}
			this.$http.post('/order/addorderform', data)
				.then((res) => {
					alert('成功提交订单');
					this.$router.push('/index');
				})
				.catch((e) => {
					console.log("提交订单失败");
				})
		}
	}
}
</script>

<style scoped>
	.seat-container {
		width: 220px;
		margin: 0 auto;
	}
	p {
		margin: 0;
	}
	span {
		width: 20px;
		height: 20px;
	}
	#screen {
		background-color: #ffff8c;
		width: 180px;
		margin: 10px auto;
		color: white;
		text-align: center;
	}
	.avalible {
		display: inline-block;
		border: 1px #dadada solid;
		background-color: white;
	}
	.avalible:hover {
		background-color: lightgreen;
		cursor: pointer;
	}
	.seleceted {
		display: inline-block;
		border: 1px #dadada solid;
		background-color: lightgreen;
	}
	.sold {
		display: inline-block;
		border: 1px #dadada solid;
		background-color: red;
	}

	.warning {
		margin-top: 20px;
		text-align: center;
	}
	button {
		position: relative;
		left: 45%;
	}
</style>


